import React from 'react'
import './index.less'
import axios from 'axios'
import { baseUrl } from '../../config';
export default class Goods extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            products: [],
        }
    }
    componentDidMount() {
        // 获取商户信息
        axios.get(
            'http://www.almondlee.cn/api/get_products',
            {
                params: {
                    id_seller: this.props.id,
                    order: 'id',
                    // lastProduct,
                    // orderValue: lastProduct
                }
            })
            .then((Response) => {
                //return data数据
                console.table(Response.data);
                this.setState(Response.data)
                console.table('state', this.state);

            })
            .catch((err) => {
                console.log(err);
            })

    }
    render() {
        return (
            <div className='goods'>
                <ul className="vproductShow-card-container">
                    {this.state.products.map(p =>
                        <li className="vproductShow-card" id={p.id}>
                            <img src={`${baseUrl}/${p.productDatas[0]}`} alt=''></img>
                            {p.product_name}
                        </li>
                    )}
                </ul>
            </div>

        )
    }
}